<template>
    <div class="main-container">
        <el-container>
            <el-header>
                <h3 class="credit-title">CRM实体元数据查询</h3>
                <el-divider></el-divider>
            </el-header>
            <el-main>
                <el-form :model="input" :rules="rules" ref="form" label-position="left">
                    <!-- 输入行 -->
                    <el-row :gutter="24">
                        <!-- 环境： -->
                        <el-col :span="8">
                            <el-form-item prop="envirFrom" label="环境：" label-width="80px">
                                <el-select size="small" v-model="input.envirFrom" placeholder="请选择" :disabled="loading"
                                    @change="envirFromChange">
                                    <el-option v-for="item in environments" :key="item.key" :label="item.label"
                                        :value="item.key">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>

                        <!-- 实体名称 -->
                        <el-col :span="8">
                            <el-form-item prop="entityName" label="实体名称" label-width="80px" required>
                                <el-input size="small" placeholder="" :disabled="loading || entityOptionsLoading"
                                    readonly v-model="input.entityName" style="vertical-align: middle;"
                                    class="entityName-input-with-select">
                                    <el-select size="small" slot="prepend" clearable filterable
                                        :loading="loading || entityOptionsLoading" v-model="input.entityName"
                                        placeholder="请选择" :filter-method="entitySelectFilter">
                                        <el-option v-for="item in entityOptions" :key="item.key" :label="item.label"
                                            :value="item.key">
                                        </el-option>
                                    </el-select>
                                </el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row :gutter="24">
                        <!-- 字段类型 -->
                        <el-col :span="8">
                            <el-form-item prop="attributeType" label="字段类型" label-width="80px">
                                <el-select size="small" clearable v-model="input.attributeType" placeholder="请选择"
                                    :disabled="loading || fieldOptionsLoading">
                                    <el-option v-for="item in fieldOptions" :key="item.key" :label="item.label"
                                        :value="item.key">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>

                        <!-- 按钮 -->
                        <el-col :span="4">
                            <el-form-item>
                                <el-button size="small" :loading="loading" @click="getData(null)">查询</el-button>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
                <!-- 展示内容 -->
                <el-row style="margin-top: 15px">
                    <el-row :gutter="10">
                        <el-col :span="24">
                            <el-card class="box-card">
                                <!-- 过滤行 -->
                                <el-row :gutter="24">
                                    <el-col :offset="20" :span="4">
                                        <el-input v-model="input.search" :disabled="loading" size="mini" clearable
                                            placeholder="输入关键字搜索" @change="dataSearchOnChange" />
                                    </el-col>
                                </el-row>
                                <!-- 数据展示 -->
                                <el-row :gutter="24" style="margin-top: 15px">
                                    <el-col :span="24">
                                        <el-table :height="tableHeight" :data="tableData.ecFrom"
                                            :key="tableKey + '_envirFrom'" border style="width: 100%"
                                            v-loading="loading"
                                            :default-sort="{ prop: 'logicalName', order: 'ascending' }">
                                            <!-- 行号 -->
                                            <el-table-column type="index" align="center" show-overflow-tooltip>
                                            </el-table-column>
                                            <!-- 逻辑名 -->
                                            <el-table-column prop="logicalName" label="逻辑名" sortable
                                                show-overflow-tooltip width="220">
                                            </el-table-column>
                                            <!-- 显示名称 -->
                                            <el-table-column prop="displayName" label="显示名称" show-overflow-tooltip
                                                width="220">
                                            </el-table-column>
                                            <!-- 字段类型 -->
                                            <el-table-column prop="attributeType" label="字段类型" show-overflow-tooltip
                                                width="120">
                                            </el-table-column>
                                            <!-- String-最大长度 -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType == 5"
                                                prop="strLength" label="最大长度" show-overflow-tooltip width="180">
                                            </el-table-column>
                                            <!-- String-格式 -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType == 5"
                                                prop="strFormat" label="格式" show-overflow-tooltip width="180">
                                            </el-table-column>
                                            <!-- Boolean-默认值 -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType === 0"
                                                prop="defaultValueStr" label="默认值" show-overflow-tooltip width="180">
                                            </el-table-column>
                                            <!-- DateTime-格式 -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType == 1"
                                                prop="dateTimeFormat" label="格式" show-overflow-tooltip width="180">
                                            </el-table-column>
                                            <!-- DateTime-行为 -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType == 1"
                                                prop="dateTimeBehavior" label="行为" show-overflow-tooltip width="180">
                                            </el-table-column>
                                            <!-- Number-精度 -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType == 2"
                                                prop="precision" label="精度" show-overflow-tooltip width="120">
                                            </el-table-column>
                                            <!-- Number-最小值 -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType == 2"
                                                prop="minimum" label="最小值" show-overflow-tooltip width="180">
                                            </el-table-column>
                                            <!-- Number-最大值 -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType == 2"
                                                prop="maximum" label="最大值" show-overflow-tooltip width="180">
                                            </el-table-column>
                                            <!-- Number-是否Money -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType == 2"
                                                prop="isMonryStr" label="是否Money" show-overflow-tooltip width="100">
                                            </el-table-column>
                                            <!-- Picklist-选项值 -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType == 4"
                                                prop="optionsStr" label="选项值" show-overflow-tooltip width="600">
                                                <template slot-scope="scope">
                                                    <div class="text-content text-ellipsis">
                                                        <div class="text-inner text-ellipsis">
                                                            <span>{{ scope.row.optionsStr }}</span>
                                                        </div>
                                                        <i class="el-icon-copy-document text-icon"
                                                            style="cursor: pointer;" title="复制"
                                                            @click="copyToClipboard(scope.row.optionsStr)"></i>
                                                    </div>
                                                </template>
                                            </el-table-column>
                                            <!-- Lookup-关联实体逻辑名 -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType == 3"
                                                prop="linkedEntityName" label="关联实体逻辑名" show-overflow-tooltip
                                                width="220">
                                            </el-table-column>
                                            <!-- Lookup-关联实体名称 -->
                                            <el-table-column
                                                v-if="!rtcrm.isNull(input.attributeType) && input.attributeType == 3"
                                                prop="linkedEntityDisplayName" label="关联实体名称" show-overflow-tooltip
                                                width="220">
                                            </el-table-column>
                                            <!-- 字段要求 -->
                                            <el-table-column prop="requiredLevel" label="字段要求" show-overflow-tooltip>
                                            </el-table-column>
                                        </el-table>
                                    </el-col>
                                </el-row>
                            </el-card>
                        </el-col>
                    </el-row>
                </el-row>
            </el-main>
        </el-container>
    </div>
</template>

<script>
export default {
    name: 'CRMEntityMetadata',
    data() {
        // 实体名称不能为空
        const entityName_rule = (rule, value, callback) => {
            if (this.rtcrm.isNullOrWhiteSpace(value)) {
                callback(new Error("实体名称不能为空不能为空！"));
            } else {
                callback();
            }
        };
        return {
            msg: "hello world",
            // 环境
            environments: [{ label: "无效环境请刷新", key: "undefined" }],
            // 实体名称
            entityOptions: [],
            entityOptionsCopy: [],
            // 字段类型
            fieldOptions: [],
            // 输入
            input: {
                envirFrom: "dev",
                entityName: "",
                attributeType: "",
                search: "",
            },
            tableData: {
                ecFrom: [],
                ecFromCopy: [],
                ecFromTotalRecord: 0,
            }, //数据
            tableHeight: "450px", //表格高度
            tableKey: 1, //刷新表格的Key
            loading: false, //是否加载数据中
            entityOptionsLoading: false, //是否加载实体名称中
            fieldOptionsLoading: false, //是否加载字段类型中
            // 表单校验规则
            rules: {
                entityName: [{ validator: entityName_rule, trigger: "change" }],
            },
        };
    },
    created() {
        //获取系统参数
        this.getEnvironments();

        //获取实体名称
        this.getEntityOptions();

        //获取字段类型
        this.getFieldOptions();
    },
    mounted() {
        //监听环境参数切换
        this.$on('environment-change', this.environmentChange);
    },
    computed: {
    },
    methods: {
        //环境切换事件
        environmentChange: function (envir) {
            if (this.rtcrm.isNullOrWhiteSpace(envir)) return;

            //清空数据
            this.$set(this.tableData, "ecFrom", []);
            this.$set(this.tableData, "ecFromTotalRecord", 0);
            this.$set(this, "input", {
                envirFrom: "dev",
                entityName: "",
                attributeType: "",
                search: "",
            });
            this.$set(this, "tableKey", this.tableKey + 1); //刷新Table

            //重新获取环境参数
            this.getEnvironments();

            //重新获取实体名称
            this.getEntityOptions();
        },

        //获取环境参数
        getEnvironments: function () {
            let _this = this;
            this.$set(this, "loading", true);

            this.jshelper
                .invokeHiddenApiAsync(
                    "new_hbxn_common",
                    "SyncConfiguration/GetEnvironments",
                    null
                )
                .then((res) => {
                    _this.$set(_this, "loading", false);
                    if (this.rtcrm.isNull(res) || this.rtcrm.isNull(res.data)) {
                        this.jshelper.openAlertDialog(this,
                            "返回数据为空", "获取环境参数"
                        );
                        return;
                    }
                    if (res.isSuccess) {
                        let data = res.data;
                        if (!this.rtcrm.isNull(data)) _this.$set(_this, "environments", data);
                    } else {
                        this.jshelper.openAlertDialog(this, res.message, "获取环境参数");
                    }
                })
                .catch((err) => {
                    _this.$set(_this, "loading", false);
                    _this.jshelper.openAlertDialog(_this, err.message, "获取环境参数");
                });
        },

        //获取实体名称
        getEntityOptions: function () {
            let _this = this;
            this.$set(this, "loading", true);
            this.$set(this, "entityOptionsLoading", true);
            this.$set(this, "entityOptions", []);
            this.$set(this, "entityOptionsCopy", []);

            let input = {
                envir: this.input.envirFrom,
                isCustomEntity: false,
            };
            this.jshelper
                .invokeHiddenApiAsync(
                    "new_hbxn_common",
                    "RetrieveEntityMetadata/GetAllEntityMetadata",
                    JSON.stringify(input)
                )
                .then((res) => {
                    _this.$set(_this, "loading", false);
                    _this.$set(_this, "entityOptionsLoading", false);
                    if (this.rtcrm.isNull(res) || this.rtcrm.isNull(res.data)) {
                        this.jshelper.openAlertDialog(this,
                            "返回数据为空", "获取实体列表"
                        );
                        return;
                    }
                    if (res.isSuccess) {
                        let data = res.data;
                        if (!this.rtcrm.isNull(data)) {
                            _this.$set(_this, "entityOptions", data);
                            _this.$set(_this, "entityOptionsCopy", data);
                        }
                    } else {
                        this.jshelper.openAlertDialog(this, res.message, "获取实体列表");
                    }
                })
                .catch((err) => {
                    _this.$set(_this, "loading", false);
                    _this.jshelper.openAlertDialog(_this, err.message, "获取实体列表");
                });
        },

        //获取字段类型
        getFieldOptions: function () {
            let _this = this;
            this.$set(this, "loading", true);
            this.$set(this, "fieldOptionsLoading", true);
            this.$set(this, "fieldOptions", []);

            this.jshelper
                .invokeHiddenApiAsync(
                    "new_hbxn_common",
                    "RetrieveEntityMetadata/GetAttributeTypeList",
                    null
                )
                .then((res) => {
                    _this.$set(_this, "loading", false);
                    _this.$set(_this, "fieldOptionsLoading", false);
                    if (this.rtcrm.isNull(res) || this.rtcrm.isNull(res.data)) {
                        this.jshelper.openAlertDialog(this,
                            "返回数据为空", "获取字段类型"
                        );
                        return;
                    }
                    if (res.isSuccess) {
                        let data = res.data;
                        if (!this.rtcrm.isNull(data)) _this.$set(_this, "fieldOptions", data);
                    } else {
                        this.jshelper.openAlertDialog(this, res.message, "获取字段类型");
                    }
                })
                .catch((err) => {
                    _this.$set(_this, "loading", false);
                    _this.jshelper.openAlertDialog(_this, err.message, "获取字段类型");
                });
        },

        //查询
        getData: function (envir) {
            let _this = this;
            this.$refs["form"].validate(async (valid, error) => {
                if (valid) {
                    //#region 清空数据
                    this.$set(this.tableData, "ecFrom", []);
                    this.$set(this.tableData, "ecFromCopy", []);
                    this.$set(this.tableData, "ecFromTotalRecord", 0);
                    this.$set(this.input, "search", "");
                    this.$set(this, "tableKey", this.tableKey + 1); //刷新Table
                    //#endregion

                    //#region 查询数据
                    this.$set(this, "loading", true);
                    let input = {
                        envir: this.input.envirFrom,
                        entityName: this.input.entityName,
                        attributeType: this.input.attributeType
                    };
                    this.jshelper
                        .invokeHiddenApiAsync("new_hbxn_common", "RetrieveEntityMetadata/GetAllAttributeMetadataFromEntity", JSON.stringify(input))
                        .then((res) => {
                            _this.$set(_this, "loading", false);
                            if (this.rtcrm.isNull(res) || this.rtcrm.isNull(res.data)) {
                                this.jshelper.openAlertDialog(this,
                                    "返回数据为空", "CRM实体字段元数据查询"
                                );
                                return;
                            }
                            if (res.isSuccess) {
                                let data = res.data;
                                _this.$set(_this.tableData, "ecFrom", data);
                                _this.$set(_this.tableData, "ecFromCopy", data);
                            } else {
                                this.jshelper.openAlertDialog(this, res.message, "CRM实体字段元数据查询");
                            }
                            _this.$set(_this, "tableKey", _this.tableKey + 1); //刷新Table
                        })
                        .catch((err) => {
                            _this.$set(_this, "loading", false);
                            _this.jshelper.openAlertDialog(_this, err.message, "CRM实体字段元数据查询");
                        });
                    //#endregion
                }
            });
        },

        //获取环境的Label
        GetEnvirLabel: function (val) {
            let obj = { label: "无效环境请刷新", key: "undefined" };
            let _obj = this.environments.find((item) => {
                return item.key === val;
            });
            return !this.rtcrm.isNull(_obj) ? _obj : obj;
        },

        //envirFrom下拉Change事件
        envirFromChange: function () {
            //重新获取实体名称
            this.getEntityOptions();
        },

        //showMessage
        showMessage(message, type, dangerouslyUseHTMLString = false) {
            this.$message({
                message: message,
                dangerouslyUseHTMLString: dangerouslyUseHTMLString,
                type: type,
            });
        },

        //实体名称下拉搜索事件
        entitySelectFilter(val) {
            if (val) {
                this.entityOptions = this.entityOptionsCopy.filter((item) => {
                    if (!!~item.label.indexOf(val) || !!~item.label.toUpperCase().indexOf(val.toUpperCase())) {
                        return true
                    }
                    else if (!!~item.key.indexOf(val) || !!~item.key.toUpperCase().indexOf(val.toUpperCase())) {
                        return true
                    }
                })
            } else {
                this.entityOptions = this.entityOptionsCopy;
            }
        },

        //table模糊搜索时间
        dataSearchOnChange(val) {
            if (this.rtcrm.isNullOrWhiteSpace(val)) {
                this.$set(this.tableData, "ecFrom", this.tableData.ecFromCopy);
            }
            else {
                this.$set(this.tableData, "ecFrom", []);//置空再重新填充数据
                for (let item of this.tableData.ecFromCopy) {
                    if (item.logicalName.indexOf(val) !== -1 || item.displayName.indexOf(val) !== -1)
                        this.tableData.ecFrom.push(item);
                }
            }
            this.$set(this, "tableKey", this.tableKey + 1); //刷新Table
        },

        //复制到剪贴板
        copyToClipboard(val) {
            var textarea = document.createElement('textarea');
            textarea.style.position = 'fixed';
            textarea.style.opacity = 0;
            textarea.value = val;
            document.body.appendChild(textarea);
            textarea.select();
            document.execCommand('copy');
            document.body.removeChild(textarea);

            this.showMessage("复制成功", "success");
        },
    },
};
</script>

<style scoped>
.main-container {
    min-height: 100%;
    background-color: white;
}

.credit-title {
    margin-top: 15px;
    text-align: center;
}

.required {
    color: red;
}

.text {
    font-size: 14px;
}

.item {
    margin-bottom: 18px;
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}

.clearfix:after {
    clear: both;
}

.box-card {
    width: 100%;
}

.entityName-input-with-select {
    width: 300px;
    vertical-align: middle;

    .el-select {
        width: 160px;
    }

    .el-input-group__append,
    .el-input-group__prepend {
        color: black;
        background-color: #fff;
    }
}

.text-content {
    position: relative;
    width: 100%;
}

.text-ellipsis {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.text-inner {
    display: inline-block;
    width: 93%;
}

.text-icon {
    width: 4%;
    text-align: right;
    vertical-align: super;
}

.text-icon:hover {
    color: #409EFF;
}
</style>


